/* eslint-disable react/no-danger */
import dynamic from 'next/dynamic';
import React, { memo, useCallback, useEffect, useState } from 'react';
import { I_Post } from '@containers/Home/types';
import { getDetailPost } from '@utils/xhrApi';
import { DialogPostWrapper } from './styled';

const DialogContainer = dynamic(() => import('@components/DialogContainer'));
const LoadingContainer = dynamic(() => import('@components/LoadingContainer'));

interface Props {
  idPost: string;
  visible: boolean;
  listPost: I_Post[];
  isUseDefaultStyle?: boolean;
  onCancel: () => void;
}

const DialogPostWithoutUseRedux = ({
  idPost,
  visible,
  onCancel,
  isUseDefaultStyle = true,
  listPost = [],
}: Props) => {
  const [loading, setLoading] = useState(false);
  const [detailPost, setDetailPost] = useState<I_Post | undefined>(undefined);

  const onGetDetailPost = useCallback(async () => {
    setLoading(true);
    try {
      let post = listPost.find(item => item.id === idPost);
      if (!post) {
        const { data } = await getDetailPost(idPost);
        post = data.data;
      }
      setDetailPost(post);
    } catch (err) {
      console.log(err);
      setDetailPost(undefined);
    } finally {
      setLoading(false);
    }
  }, [idPost, listPost]);

  useEffect(() => {
    if (idPost) onGetDetailPost();
    else setDetailPost(undefined);
    return () => {
      setLoading(false);
      setDetailPost(undefined);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [idPost]);

  return (
    <DialogContainer
      visible={visible}
      onCancel={onCancel}
      maskClosable
      top={0}
      width="700px"
      // height="80%"
      centered
      bodyStyle={{ maxWidth: '700px', width: '100%' }}
    >
      <DialogPostWrapper isUseDefaultStyle={isUseDefaultStyle}>
        <LoadingContainer loading={loading}>
          <div className="header">
            {detailPost?.thumbnail?.src && (
              <img
                className="thumb"
                src={detailPost?.thumbnail?.src}
                alt={detailPost?.thumbnail?.name}
              />
            )}
            <div className="title">{detailPost?.title || 'N/A'}</div>
          </div>
          <div className="body hide-scrollbar">
            {detailPost?.content && (
              <div
                className="content ctn__html"
                dangerouslySetInnerHTML={{ __html: detailPost?.content }}
              />
            )}
          </div>
        </LoadingContainer>
      </DialogPostWrapper>
    </DialogContainer>
  );
};

export default memo(DialogPostWithoutUseRedux);
